header {
  position: relative;
  height: 100vh;
  min-height: 100vh;
  width: 100%;
  background-color: #161415;
  text-align: center;
  overflow: hidden; }
  header .header-image {
    background: #161415 url(/images/background.jpg) no-repeat top center;
    filter: blur(0.75rem);
    -webkit-filter: blur(0.75rem);
    height: 100%;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover !important;
    -webkit-background-size: cover !important;
    position: absolute;
    top: 0;
    left: 0;
    right: 0; }
  header:before {
    content: "";
    display: inline-block;
    vertical-align: middle;
    height: 100%; }
  header .scrolldown a {
    position: absolute;
    bottom: 1.875rem;
    left: 50%;
    margin-left: -1.8125rem;
    color: #ffffff;
    display: block;
    height: 2.625rem;
    width: 2.625rem;
    font-size: 2.625rem;
    line-height: 2.625rem;
    border-radius: 100%;
    transition: all 0.3s ease-in-out; }
    header .scrolldown a:hover {
      color: #989898; }

